.contenido{
    background-color: #222;
    width: 100vw;
    height: 100vh;
}

.luzSuperior{
    width: 200px;
    height: 60px;
    background: rgba(54, 150, 194, 0.972);
    position: absolute;
    top: 0;
    right: 0;
    filter: blur(300px);
}



/* Nav */
.logoN, .logoF{
    font-weight: bold;
    color: rgb(192, 87, 241);
    margin-left: 20px;
    text-shadow: 1px 1px 20px rgb(192, 87, 241);
}
.logoF{
    position: absolute;
    top: 0;
    left: 40%;
}
.tituloLogo{
    font-weight: bold;
    padding-top: 2px;
}


/* Main */
h1{
    font-size: 260%;
}
.colorNFT{
    color: rgb(192, 87, 241);
    text-shadow: 1px 1px 2px rgb(192, 87, 241);

}
.colorT{
    color:  rgba(160, 35, 243, 0.972);
    text-shadow: 1px 1px 2px rgba(160, 35, 243, 0.972);

}
.btnNFT{
    color: rgb(192, 87, 241);
    background: none;
    border: 0px;
    text-shadow: 0px 0px 2px rgb(192, 87, 241);
}
.btnNFT:hover{
    border: 1px solid rgb(192, 87, 241);
    border-radius: 20px;
    padding: 7px 10px;
    transition: all 200ms;
    box-shadow: 0px 0px 5px 0px rgb(192, 87, 241);
}
.btnExplorar, .btnExplorarBack{
    color: #eee;
    border: 0px;
    padding: 8px 13px;
    border-radius: 20px;
}
.btnExplorar{
    background: #222;
}
.btnExplorarBack{
    background: linear-gradient(14deg, rgba(95,38,132,1) 0%, rgba(155,111,170,1) 48%, rgba(214,82,206,1) 100%);
    transform: scale(1.01);
}


@media screen and (min-width:400px) {
    .escala{
        transform: scale(1.05);
        width: 400px;
        margin-left: 5%;
    }
}
@media screen and (min-width:900px) {
    .escala{
        transform: scale(1.3);
        margin-top: 10%;
        margin-right: 2%;
        width: 40%;
    }
}

.contenedorCard{
    margin-left: 40%;
}
.cardColor, .cardColor2, .cardColor3{
    position: absolute;
    background: #222;
    max-width: 400px;
    min-width: 340px;
    top: 18%;
    right: 59%;
    height: 500px;
    border-radius: 9%;
    border: 4px solid rgba(155,111,170,1);
    box-shadow: 0px 30px 120px rgba(160, 35, 243, 0.972);
    z-index: 4;
}
.cardColor{
    transform: rotate(-10deg);
    box-shadow: 0px -60px 120px rgba(54, 150, 194, 0.972);
    z-index: 2;
    right: 61%;
    margin-top: 30px;

}
.cardColor3{
    right: 63%;
    z-index: 0;
    transform: rotate(-20deg);
    top: 25%;
}

.imgtop{
    margin-top: -40px;
    z-index: 9;
}
.cuadroTextoCard{
    width: 250px;
    height: 80px;
    background: rgba(17, 17, 17, 0.959);
    border-radius: 80px;
    margin-left: 13%;
    z-index: 10;
    position: absolute;
    top: 50%;
    border: 1px solid rgba(54, 150, 194, 0.972);
}
.imgPerfil{
    border-radius: 50%;
    margin-top: 15px;
    margin-left: 15px;
}
.peU{
    margin-top: -45px;
    margin-left: 85px;
}
.peD{
    margin-top: -20px;
    margin-left: 85px;
}
.pwi{
    font-size: 13px;
}
.lineaMorada{
    width: 100px;
    height: 4px;
    background: linear-gradient(14deg, rgba(95,38,132,1) 0%, rgba(155,111,170,1) 48%, rgba(214,82,206,1) 100%);
    border-radius: 40px;
    margin-left: 35%;
    box-shadow: 0px 0px 10px 0px rgba(95,38,132,1);
}



/* Footer */
footer{
    width: 100vw;
    height: 50px;
    font-size: 20px;
    font-family: sans-serif;
    font-weight: bold;
    color: #444;
    position: absolute;
    display: flex;
    bottom: 0;
    justify-content: space-around;
}